"use client";

import React, { useRef } from 'react';
import Image from "next/image";

const der = "/icons/derecha.svg";
const izq = "/icons/izquierda.svg";
const pic1 = "/pic1.jpg";
const pic2 = "/pic2.jpg";
const pic3 = "/pic3.jpg";

const articles = [
  {
    title: 'Guía de iniciación de Fatal Fury: City of the Wolves',
    description:
      'Si sois neofuturistas, quizá no imagináis o entendéis la magnitud de Fatal Fury: City of the Wolves ni por qué hay tanto expectación ante el inminente regreso de esta saga de SNK...',
    image: `${pic1}`,
  },
  {
    title: 'Terror japonés',
    description:
      'El último capítulo de la historia original del universo de Dead by Daylight, Doodmed Course, recuerda más a los historias de piratas (en parte por su estética, la Adiestradora Canina)...',
    image: `${pic2}`,
  },
  {
    title: 'Terror japonés',
    description:
      'El último capítulo de la historia original del universo de Dead by Daylight, Doodmed Course, recuerda más a los historias de piratas (en parte por su estética, la Adiestradora Canina)...',
    image: `${pic3}`,
  },
  {
    title: 'Guía de Assassin’s Creed Shadows',
    description:
      'Assassin’s Creed Shadows lleva a los jugadores a una vieja inolvidable por un Japón en guerra durante el periodo Sengoku. Os enfrentaréis a una enorme cantidad de enemigos...',
    image: `${pic1}`,
  },
  {
    title: 'Guía de Assassin’s Creed Shadows',
    description:
      'Assassin’s Creed Shadows lleva a los jugadores a una vieja inolvidable por un Japón en guerra durante el periodo Sengoku. Os enfrentaréis a una enorme cantidad de enemigos...',
    image: `${pic1}`,
  },
];

const ArticulosDestacados = () => {
  const scrollRef = useRef<HTMLDivElement>(null);

  const scroll = (direction: 'left' | 'right') => {
    if (scrollRef.current) {
      const scrollAmount = scrollRef.current.offsetWidth; // scroll por 3 tarjetas
      scrollRef.current.scrollBy({
        left: direction === 'left' ? -scrollAmount : scrollAmount,
        behavior: 'smooth',
      });
    }
  };

  return (
    <div className='w-full bg-dark text-white py-10 px-6 overflow-hidden'>
      <div className='max-w-7xl mx-auto'>
        {/* Header */}
        <div className='flex justify-between items-center mb-6'>
          <h2 className='text-xl font-primary'>Artículos destacados</h2>
          <div className='flex items-center gap-2'>
            <a href='#' className='text-primary font-primary text-xl hover:underline'>
              Ver todos
            </a>
            <button onClick={() => scroll('left')}>
              <Image src={izq} alt="Flecha izquierda" width={44} height={44} />
            </button>
            <button onClick={() => scroll('right')}>
              <Image src={der} alt="Flecha derecha" width={44} height={44} />
            </button>
          </div>
        </div>

        {/* Carrusel */}
        <div
          ref={scrollRef}
          className='flex gap-6 overflow-x-auto no-scrollbar scroll-smooth'
        >
          {articles.map((article, index) => (
            <div
              key={index}
              className='w-[calc(33.333%-1rem)] bg-dark rounded-xl overflow-hidden shadow-md flex-shrink-0 relative'
            >
              <img
                src={article.image}
                alt={article.title}
                className='w-full h-[240px] object-cover rounded-4xl'
              />
              <div className='p-4 pb-16'>
                <h3 className='text-text text-xl font-primary mb-2 leading-snug'>
                  {article.title}
                </h3>
                <p className='text-sm text-text font-primary line-clamp-3 leading-snug'>
                  {article.description}
                </p>
              </div>
              <button className='absolute bottom-4 left-4 bg-azulsub text-text text-xs font-primary px-6 py-3 rounded-2xl hover:bg-gray-200 transition'>
                LEER MÁS
              </button>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default ArticulosDestacados;
